  <as-row>
    <as-checkbox v-model="value1" label="1">单选</as-checkbox>
  </as-row>
  <as-row>
    <as-checkbox v-model="value1" label="1">单选1</as-checkbox>
    <as-checkbox v-model="value1" label="2">单选2</as-checkbox>
    <as-checkbox v-model="value1" label="3">单选3</as-checkbox>
