    <span class="hljs-tag">&lt;<span class="hljs-name">as-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>单选2<span class="hljs-tag">&lt;/<span class="hljs-name">as-checkbox</span>&gt;</span>
