<h3>自定义label</h3>
<p>除了更换label外，还是需要设置label(此时的label不显示，但是作为选择判断)</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row>
    <as-checkbox v-model="value1" label="1"><as-icon name="sexw"></as-icon></as-checkbox>
    <as-checkbox v-model="value1" label="2"><as-icon name="sexm"></as-icon></as-checkbox>
  </as-row>
  <as-row>
    <as-checkbox v-model="value1" label="1" border=""><as-icon name="sexw"></as-icon></as-checkbox>
    <as-checkbox v-model="value1" label="2" border=""><as-icon name="sexm"></as-icon></as-checkbox>
  </as-row>

  

